import {
  AfterContentInit,
  AfterViewChecked,
  ChangeDetectionStrategy,
  Component,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { NavigationEnd, NavigationStart, Router } from '@angular/router';
import { SwUpdate } from '@angular/service-worker';
import { ScullyRoute, ScullyRoutesService } from '@scullyio/ng-lib';
import { combineLatest, defer, from, of } from 'rxjs';
import { Observable, fromEvent, Subject } from 'rxjs';
import { tap, takeUntil, switchMapTo, catchError, map, mapTo, pairwise, startWith, timeout, distinctUntilChanged, switchMap, pluck } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { HighlightService } from '../shared/services/highlight.service';
import { MetaService } from '../shared/services/meta.service';
import { Frontmatter } from '../shared/shared.model';

declare var ng: any;
declare let gtag: Function;

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.scss'],
  preserveWhitespaces: true,
  encapsulation: ViewEncapsulation.Emulated,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BlogComponent
  implements OnInit, AfterContentInit, AfterViewChecked, OnDestroy {
  @ViewChild('scully') scullyElement: any;

  title: string;
  blog$: Observable<ScullyRoute> = this.scullyRoutesService.getCurrent().pipe(
    tap(res => {
      gtag('event', 'page_view', { 'page_title': res.title,'page_path': res.route });
    })
  );

  allowHighlight = true;

  private destroy$ = new Subject();
  constructor(
    private router: Router,
    private highlightService: HighlightService,
    private readonly swUpdate: SwUpdate,
    private metaService: MetaService,
    private scullyRoutesService: ScullyRoutesService
  ) { }

  ngOnInit(): void {

    fromEvent(window, 'AngularReady')
      .pipe(
        takeUntil(this.destroy$),
        tap(() => (this.allowHighlight = false))
      )
      .subscribe(res => {
        // console.log('AngularReady', res);
      });

    
  }

  ngAfterContentInit(): void {
  }

  ngAfterViewChecked(): void {
    if (this.allowHighlight) {
      this.highlightService.highlightAll();
    }

    
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
